export default {
  // tab菜单
  SET_TAB_MENUS(state, list) {
    state.tabMenus = list
  },

  // 侧栏菜单
  SET_SIDE_MENUS(state, data) {
    state.sideMenus = data
  },

  // 按钮级权限id集合
  SET_BTN_AUTHORITY_IDS(state, list) {
    state.btnAuthorityIds = list
  },

  // 菜单Url集合
  SET_MENU_AUTHORITY_URLS(state, list) {
    state.menuAuthorityUrls = list
  },

  // 用户的默认跳转页面路径（因为没有首页，需要取到用户拥有权限的页面作默认跳转页）
  SET_DEFAULT_PATH(state, path) {
    state.defaultPath = path
  },

  SET_ORG_LIST(state, list) {
    state.orgList = list
  }
}